"use client";

import { Button } from "@/components/ui/button";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { signOut } from "next-auth/react";

interface DashboardLayoutProps {
  children: React.ReactNode;
  userRole: "STUDENT" | "TUTOR";
}

export function DashboardLayout({ children, userRole }: DashboardLayoutProps) {
  const pathname = usePathname();

  const studentNavItems = [
    { href: "/dashboard", label: "Overview" },
    { href: "/dashboard/sessions", label: "My Sessions" },
    { href: "/dashboard/exercises", label: "Exercises" },
    { href: "/dashboard/documents", label: "Documents" },
  ];

  const tutorNavItems = [
    { href: "/dashboard", label: "Overview" },
    { href: "/dashboard/sessions", label: "Sessions" },
    { href: "/dashboard/students", label: "Students" },
    { href: "/dashboard/exercises", label: "Exercises" },
    { href: "/dashboard/documents", label: "Documents" },
  ];

  const navItems = userRole === "STUDENT" ? studentNavItems : tutorNavItems;

  return (
    <div className="min-h-screen flex">
      <aside className="w-64 border-r p-4">
        <div className="space-y-4">
          <div className="p-2">
            <h2 className="text-lg font-semibold">TutorHub</h2>
            <p className="text-sm text-muted-foreground">
              {userRole === "STUDENT" ? "Student Dashboard" : "Tutor Dashboard"}
            </p>
          </div>
          <nav className="space-y-1">
            {navItems.map((item) => (
              <Button
                key={item.href}
                variant={pathname === item.href ? "secondary" : "ghost"}
                className="w-full justify-start"
                asChild
              >
                <Link href={item.href}>{item.label}</Link>
              </Button>
            ))}
          </nav>
        </div>
        <div className="mt-auto">
          <Button
            variant="ghost"
            className="w-full justify-start"
            onClick={() => signOut()}
          >
            Sign Out
          </Button>
        </div>
      </aside>
      <main className="flex-1 p-8">{children}</main>
    </div>
  );
} 